*{
    margin: 0;
    padding:0;
    box-sizing: border-box;

}


.App{
    
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;font-weight: 100;
  font-size: 15px;

    
}


.Card{
    height: 500px;
    width: 400px;
    border: 29px rgba(152, 143, 238, .5) solid;
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);

}


.image-container{
  border-radius: 50%;
 
    width: 150px;
    height: 150px;
    overflow: hidden;
    display: flex;
    justify-content: center;


}




.upper-container{
height: 50%;
  
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(./flowers.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  color: antiquewhite;

}
.lower-container{
height: 50%;

 

}
.name_buttons{
    height: 50%;
   display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
    color: rgb(84, 79, 153);  
 
     font-size: 10px;
 
}
.name_buttons button:hover{
    background-color: rgb(155, 124, 155);

}
.name_buttons button:active{
    background-color: rgb(133, 74, 133);

}




.button-info button{
    height: 30px;
    width: 50px;
    margin: 10px;
    outline: none;
    border: none;
    background-color: rgb(136, 136, 177);
    border-radius: 3px;
    color: aliceblue;
    font-weight: 700;
    cursor: pointer;
}



.lower-lower{
    height: 50%;
  
    display: flex;
    flex-direction: column;
    
}


.likes{
    display: flex;
     border-top: 4px rgb(229, 209, 236) solid;
     height: 50%;


}

.heart{
    outline: none;
    border: none;
    background-color: inherit;
    font-size: 25px;
    display: block;
    color: rgb(211, 55, 55);
}

.number{
    font-size: 25px;
   
  

    
}

.button-media{
     border-top: 4px rgb(228, 205, 219) solid;
     flex: auto;

}

.button-media,.likes{
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-media >*,.likes>*{
    margin: 5px;;
}

.social1{
     outline: none;
    border: none;
    background-color: inherit;
      font-size: 25px;
        color: rgb(138, 166, 189);

}
.social0{
     outline: none;
    border: none;
    background-color: inherit;
      font-size: 35px;
      color: rgb(84, 84, 133);

}

a{
   
  text-decoration: none;
}

a:visited {color:rgb(84, 84, 133);
}

